import React from 'react';
import {Button, Input, InputNumber, Modal, Select} from 'antd';
import PropTypes from 'prop-types';
import baseAPI from '../../../../../api/baseAPI';
import ContentModal from './ContentModal';
import classNames from 'classnames';

// style
import style from '../styles/index.module.scss';
const { Option } = Select;
function ModalSearch({getList}) {
    const date = new Date()

    const [day, setDay] = React.useState(date.getDate());
    const [month, setMoth] = React.useState(date.getMonth());
    const [year, setYear] = React.useState(date.getFullYear());
    const [code, setCode] = React.useState('');

    async function handleOk(now= false) {
        const data = {};
        const dataNow = {
            day: date.getDate(),
            month: date.getMonth(),
            year: date.getFullYear(),
        }
        year && (data['year'] = year);
        month && (data['month'] = month);
        day && (data['day'] = day);
        code.length > 0 && (data['code'] = code);
        const dataPush = now ? dataNow : data
        getList(dataPush)
    }

    // function onChange(value) {
    //     console.log(`selected ${value}`);
    // }
    //
    // function onBlur() {
    //     console.log('blur');
    // }
    //
    // function onFocus() {
    //     console.log('focus');
    // }
    //
    // function onSearch(val) {
    //     console.log('search:', val);
    // }

    // const ComponentSelect = () => (
    //     <Select
    //         showSearch
    //         style={{ width: 200 }}
    //         placeholder="Select a person"
    //         optionFilterProp="children"
    //         onChange={onChange}
    //         onFocus={onFocus}
    //         onBlur={onBlur}
    //         onSearch={onSearch}
    //         filterOption={(input, option) =>
    //             option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
    //         }
    //     >
    //         <Option value="jack" >Jack</Option>
    //         <Option value="lucy">Lucy</Option>
    //         <Option value="tom">Tom</Option>
    //     </Select>
    // )
    return(
        <div className={classNames(style.wrapper_search_all)}>
            <div className={classNames(style.display_flex_end)}>
                <div className={classNames(style.text)} style={{width: 50}}>Ngày: </div>
                <InputNumber style={{width: 50}} value={day} min={0} max={31} onChange={setDay} /> {'-'}
                <InputNumber style={{width: 50}} value={month} min={0} max={12} onChange={setMoth} /> {'-'}
                <InputNumber style={{width: 75}} value={year} onChange={setYear} />
            </div>
            <div className={classNames(style.display_flex_end)}>
                <div className={classNames(style.text)}>Mã sản phẩm: </div>
                <Input value={code} onChange={(event => setCode(event.target.value))} />
            </div>
            <Button
                type="primary"
                icon="search"
                style={{width: 80, marginLeft: 10, marginRight: 10, height: 30}}
                onClick={() => handleOk()}
            />
            <Button
                type="primary"
                icon={'reload'}
                // style={{width: 80, marginLeft: 10, marginRight: 10, height: 30}}
                onClick={() => handleOk(true)}
            />
        </div>
    );
}

ModalSearch.propTypes = {
    itemsCapitalSpent: PropTypes.object,
    getList: PropTypes.func,
};

ModalSearch.defaultProps = {
    getList: () => null
};

export default ModalSearch;
