import React from 'react';
import {Input, InputNumber, message, Modal} from 'antd';
import PropTypes from 'prop-types';
import baseAPI from '../../../../../api/baseAPI';

// style
import style from '../styles/index.module.scss'
import classNames from "classnames";
import convertStringToVND from "../../../../../util/function/convertStringToVND";
function ModalAddOne({visible, setVisible, setItems, dataNow}) {
    const [data, setData] = React.useState({
        date: `${dataNow.day}/${dataNow.month}/${dataNow.year}`
    });
    function onChangeText(e, type){
        setData({...data, [type]: e.target.value})
    }
    function onChangeNumber(value, type){
        setData({...data, [type]: value})
    }

    async function handleOk() {
        const {date, code, name, number_of_products_sold, price, capital_spent_one} = data;
        const capital_spent = capital_spent_one * number_of_products_sold * 1000;
        const revenue_sum = price * number_of_products_sold * 1000;
        if(date && code && name && number_of_products_sold && price && capital_spent_one) {
            const item = {
                code: code, // Mã bán hàng
                name: name, // Tên bán hàng
                number_of_products_sold: number_of_products_sold, // Số lượng bán
                revenue: revenue_sum, // Doanh thu
                revenue_sum: revenue_sum, // Doanh thu thật
                date: date, // ngày
                capital_spent: capital_spent, // Tổng giá gốc
                profit: revenue_sum - capital_spent, // Lãi
            }
            const {items} = await baseAPI.add('/api/transaction', {
                data: [item],
            });
            const itemNew = await {...Object.values(items)[0], ...item}
            items && await setItems(itemNew._id, itemNew);
            handleCancel(false);
        } else {
            message.error('Lỗi định dạng nhập')
        }
    }

    function handleCancel() {
        setVisible(false)
        setData({});
    }
    console.log('data', data); // MongLV log fix bug
    return(
        <Modal
            title={<h1 style={{textAlign: 'center'}}>Thêm dữ liệu</h1>}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            width={window.innerWidth*0.6}
        >
            <div className={classNames('grid', style.wrapper_add_one)} style={{margin: '0 auto'}}>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Ngày: </span></div>
                    <div className={'col l-9'}><Input placeholder={'dd/mm/yyyy'} style={{width: 120}} value={data.date}  onChange={(e) => onChangeText(e, 'date')} /></div>
                </div>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Mã hàng: </span></div>
                    <div className={'col l-9'}><Input onChange={(e) => onChangeText(e, 'code')} /></div>
                </div>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Tên: </span></div>
                    <div className={'col l-9'}><Input onChange={(e) => onChangeText(e, 'name')} /></div>
                </div>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Số lượng bán: </span></div>
                    <div className={'col l-9'}>
                        <InputNumber
                            formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                            parser={value => value.replace(/\$\s?|(,*)/g, '')}
                            // onChange={onChange}
                            min={1}
                            style={{width: '100px'}}
                            onChange={(value) => onChangeNumber(value, 'number_of_products_sold')}
                        />
                    </div>
                </div>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Vốn nhập /1 đơn vị: </span></div>
                    <div className={'col l-9'}>
                        <div className={'row'}>
                            <div className={'col l-4'}>
                                <InputNumber
                                    formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                    parser={value => value.replace(/\$\s?|(,*)/g, '')}
                                    // onChange={onChange}
                                    min={1}
                                    style={{width: '100%'}}
                                    onChange={(value) => onChangeNumber(value, 'capital_spent_one')}
                                />
                            </div>
                            <div className="col l-4">
                                x 1000(vnđ) =
                            </div>
                            <div className="col l-4">
                                <span>{data && data['capital_spent_one'] ? convertStringToVND(data['capital_spent_one'] * 1000)  : '0 VND'}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={'row'}>
                    <div className={'col l-3'}><span>Giá bán/ 1 đơn vị: </span></div>
                    <div className={'col l-9'}>
                        <div className={'row'}>
                            <div className={'col l-4'}>
                                <InputNumber
                                    formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                    parser={value => value.replace(/\$\s?|(,*)/g, '')}
                                    // onChange={onChange}
                                    min={data && data['capital_spent_one'] && data ? data['capital_spent_one'] : 0}
                                    style={{width: '100%'}}
                                    onChange={(value) => onChangeNumber(value, 'price')}
                                />
                            </div>
                            <div className="col l-3">
                                x 1000(vnđ) =
                            </div>
                            <div className="col l-5">
                                <span>
                                    {
                                        data && data['price'] ?
                                            convertStringToVND(data['price'] * 1000)  : '0 VND'
                                    }
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </Modal>
    );
}

ModalAddOne.propTypes = {
    visible: PropTypes.bool,
    setVisible: PropTypes.func,
    setItems: PropTypes.func,
};

ModalAddOne.defaultProps = {
    visible: false,
    setVisible: () => null,
    setItems: () => null,
};

export default ModalAddOne;
