import React from 'react';
import {Table, Button, Tag, Popconfirm, Icon} from 'antd';
import { EditableFormRow, EditableCell } from './EditeTable';

// style
import styles from './styles/index.module.scss';

// util
import baseAPI from '../../../../api/baseAPI';
import ModalAdd from './Modal/ModalAdd';
import convertStringToVND from '../../../../util/function/convertStringToVND';
import sumNumber from '../../../../util/function/sumNumber';
import ModalSearch from './Modal/ModalSearch';
import classNames from 'classnames';
import ModalAddOne from './Modal/ModalAddOne';

const date = new Date();
const dataNow = {
    day: date.getDate(),
    month: date.getMonth(),
    year: date.getFullYear(),
}
function ExcelPageNews() {
    const [items, setItems] = React.useState({});
    const [itemsCapitalSpent] = React.useState({
        "CA17PN-445EV/2M": 15000,
        "CA17PN-612EV/2M": 300000,
        "VAN612 EV/2M": 250000,
        "NEP335 2CM": 60000,
        "CA9TL-445EV/2M": 70000,
        "NEP101 G 4CM":200000
    });
    const [visibleAdd, _setVisibleAdd] = React.useState(false);
    const [visibleAddOne, _setVisibleAddOne] = React.useState(false);
    const [visibleSearch, _setVisibleSearch] = React.useState(false);

    const handleDelete = async (key) => {
        const _items = {...items};
        delete _items[key];
        const {message} = await baseAPI.delete('/api/transaction', key);
        message && await setItems(_items);
    };
    const columns = React.useMemo(() => [
        {
            title: 'Ngày giao dịch',
            dataIndex: 'date',
            // editable: true,
        },
        {
            title: 'Mã hàng',
            dataIndex: 'code',
            // editable: true,
            render: (value) => <h4 style={{color: '#f87f7f'}}>{value}</h4>
        },
        {
            title: 'Tên',
            dataIndex: 'name',
            // editable: true,

        },
        {
            title: 'SL Bán',
            dataIndex: 'number_of_products_sold',
            // editable: true,
        },
        {
            title: 'Doanh thu',
            dataIndex: 'revenue',
            // editable: true,
            render: (value) => <Tag color={'orange'}>{convertStringToVND(value)}</Tag>
        },
        {
            title: 'SL Trả',
            dataIndex: 'number_of_returned_orders',
            // editable: true,
        },
        {
            title: 'Giá trị trả',
            dataIndex: 'value_of_returned_goods',
            // editable: true,
            render: (value) => <Tag color={'red'}>{convertStringToVND(value)}</Tag>
        },
        {
            title: 'Doanh thu thuần',
            dataIndex: 'revenue_sum',
            // editable: true,
            render: (value) => <Tag color={'orange'}>{convertStringToVND(value)}</Tag>
        },
        {
            title: 'Gía nhập',
            dataIndex: 'capital_spent',
            // editable: true,
            render: (value) => <Tag color={'blue'}>{convertStringToVND(value)}</Tag>
        },
        {
            title: 'Lợi nhuận',
            dataIndex: 'profit',
            // editable: true,
            render: (value) => <Tag color={'green'}>{convertStringToVND(value)}</Tag>
        },
    ], [items]);

    // func cache
    const setVisibleAdd = React.useCallback((isValue) => _setVisibleAdd(isValue), [visibleAdd])
    const setVisibleAddOne = React.useCallback((isValue) => _setVisibleAddOne(isValue), [visibleAddOne])
    const updateItems = React.useCallback((value) => setItems({...items, ...value}), [items]);

    // func
    const rows = React.useMemo(() => {
        return Object.values(items).sort((a, b) => new Date(a.create).getTime() - new Date(b.create).getTime());
    }, [items])

    function updateData(key='1', value={}) {
        const _items = {...items};
        _items[key] = value;
        setItems({..._items})
    }

    async function getList(data={}) {
        const items = await baseAPI.getAll('/api/transaction', data);
        items && setItems(items);
    }


    // vong doi
    React.useEffect(() => {
        getList(dataNow).then();
    }, []);

    const actionColumns = [...columns].concat([{
        title: 'Action',
        dataIndex: 'action',
        render: (text, record) =>
            (
                <Popconfirm title='Bạn có chắc muốn xóa?' onConfirm={() => handleDelete(record._id)}>
                    <Icon type='delete' theme='filled' style={{ color: 'red', fontSize: '20px' }} />
                </Popconfirm>
            )
    }])

    // JSX
    const components = {
        body: {
            row: EditableFormRow,
            cell: EditableCell,
        },
    };
    const TableSum = (array) => (<table style={{width: '100%'}} className={classNames('table_sum', styles.box_shadow)}>
        <tr>
            <th>Doanh thu</th>
            <th>Giá nhập</th>
            <th>Lợi nhuân</th>
        </tr>
        <tr>
            <th><h3 style={{color: 'orange'}}>{convertStringToVND(sumNumber(array, 'revenue'))}</h3></th>
            <th><h3 style={{color: 'blue'}}>{convertStringToVND(sumNumber(array, 'capital_spent'))}</h3></th>
            <th><h3 style={{color: 'green'}}>{convertStringToVND(sumNumber(array, 'profit'))}</h3></th>
        </tr>
    </table>);
    return(
        <div style={{ margin: 50}}>
            <div className={styles.position_fixed} style={{width: window.innerWidth*0.9}}>
                <div className={styles.display}>
                    <Button type={'primary'} onClick={() => setVisibleAddOne(true)}>Thêm</Button>
                    <Button type={'primary'} onClick={() => setVisibleAdd(true)}>Thêm bằng excel </Button>
                    <ModalSearch getList={getList} />
                </div>
            </div>

            <div style={{marginTop: 20}} />
            {
                TableSum(rows)
            }
            <div style={{marginTop: 20}} />
            <Table
                components={components}
                rowClassName={() => 'editable-row'}
                dataSource={rows}
                columns={actionColumns}
            />
            {
                visibleAddOne && (
                    <ModalAddOne dataNow={dataNow} setItems={updateData} visible={visibleAddOne} setVisible={setVisibleAddOne} />
                )
            }
            <ModalAdd TableSum={TableSum} visible={visibleAdd} setVisible={setVisibleAdd} columns={columns} itemsCapitalSpent={itemsCapitalSpent} updateItems={updateItems}/>
        </div>
    );
}

ExcelPageNews.propTypes = {};

ExcelPageNews.defaultProps = {};
export default ExcelPageNews;
