import React from 'react';
import { Button, Icon, Modal, Table, Upload } from 'antd';
import PropTypes from 'prop-types';
import { ExcelRenderer } from 'react-excel-renderer';
import baseAPI from '../../../../../api/baseAPI';
import ContentModal from './ContentModal';

function ModalAdd({visible, setVisible, columns, itemsCapitalSpent, updateItems, TableSum}) {
    const [rows, setRows] = React.useState([]);
    const [errorMessage, setErrorMessage] = React.useState('');
    async function handleOk() {
        const {items} = baseAPI.add('/transaction', {
            data: rows,
        });
        items && updateItems(items);
        setVisible(false);
    }

    function handleCancel() {
        setVisible(false)
        setRows([]);
    }
    function fileHandler(fileList) {
        console.log('fileList', fileList);
        let fileObj = fileList;
        if (!fileObj) {
            setErrorMessage('No file uploaded!')
            return false;
        }
        if (
            !(
                fileObj.type === 'application/vnd.ms-excel' ||
                fileObj.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
            )
        ) {
            setErrorMessage('Unknown file format. Only Excel files are uploaded!')
            return false;
        }
        //just pass the fileObj as parameter
        ExcelRenderer(fileObj, (err, resp) => {
            // console.table(resp.rows); // MongLV log fix bug
            if (err) {
                console.log(err);
            } else {
                let newRows = [];
                resp.rows.slice(1).map((row, index) => {
                    if (row && row !== 'undefined') {
                        console.log('row', row); // MongLV log fix bug
                        const capital_spent = Number(row[11]) * (row[4] - row[6])
                        newRows.push({
                            key: index,
                            code: row[0], // Mã bán hàng
                            name: row[1], // Tên bán hàng
                            number_of_products_sold: row[4], // Số lượng bán
                            revenue: row[5], // Doanh thu
                            number_of_returned_orders: row[6], // Số lượng trả hàng
                            value_of_returned_goods: row[7], // Số tiền hàng bị trả
                            revenue_sum: row[5] - row[7], // Doanh thu thật
                            date: row[10], // ngày
                            capital_spent: capital_spent, // Tổng giá gốc
                            profit: row[9] - capital_spent, // Lãi
                        });
                    }
                });
                if (newRows.length === 0) {
                    setErrorMessage('No data found in file!')
                    return false;
                } else {
                    setRows(newRows);
                    setErrorMessage('');
                }
            }
        });
        return false;
    }

    return(
        <Modal
            title={<h1 style={{textAlign: 'center'}}>Thêm dữ liệu</h1>}
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            width={window.innerWidth*0.9}
        >
            {
                visible && (
                    <Upload
                        name='file'
                        beforeUpload={fileHandler}
                        onRemove={() => setRows([])}
                        multiple={false}
                    >
                        <Button type={'primary'}><Icon type='upload' /></Button>
                    </Upload>
                )
            }
            <ContentModal columns={columns} rows={rows} TableSum={TableSum}/>
        </Modal>
    );
}

ModalAdd.propTypes = {
    visible: PropTypes.bool,
    setVisible: PropTypes.func,
};

ModalAdd.defaultProps = {
    visible: false,
    setVisible: () => null,
};

export default ModalAdd;
