import axiosClient from './axiosClient';
import queryString from 'query-string';
const _url = '';
const baseAPI = {
    getAll(url = _url, data) {
        const paramsString = queryString.stringify(data);
        return axiosClient.get(`${url}?${paramsString}`);
    },

    add(url = _url, data) {
        console.log('url', url); // MongLV log fix bug
        return axiosClient.post(url, data);
    },
    update(url = _url, data) {
        return axiosClient.put(url = _url, data);
    },
    delete(url = _url, id) {
        const urlDelete = `${url}/${id}`;
        return axiosClient.delete(urlDelete);
    },
};
export default baseAPI;
