import React, { Component } from 'react';
import { Table, Button, Popconfirm, Row, Col, Icon, Upload } from 'antd';
import { EditableFormRow, EditableCell } from './EditeTable';

// util
import baseAPI from '../../../../api/baseAPI';

export default class ExcelPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {},
            cols: [],
            rows: [],
            errorMessage: null,
            columns: [
                {
                    title: 'Ngày giao dịch',
                    dataIndex: 'date',
                    // editable: true,
                },
                {
                    title: 'Mã hàng',
                    dataIndex: 'code',
                    // editable: true,
                },
                {
                    title: 'Tên',
                    dataIndex: 'name',
                    // editable: true,
                },
                {
                    title: 'SL Bán',
                    dataIndex: 'number_of_products_sold',
                    // editable: true,
                },
                {
                    title: 'Doanh thu',
                    dataIndex: 'revenue',
                    // editable: true,
                },
                {
                    title: 'SL Trả',
                    dataIndex: 'number_of_returned_orders',
                    // editable: true,
                },
                {
                    title: 'Giá trị trả',
                    dataIndex: 'value_of_returned_goods',
                    // editable: true,
                },
                {
                    title: 'Doanh thu thuần',
                    dataIndex: 'revenue_sum',
                    // editable: true,
                },
                {
                    title: 'Gía nhập',
                    dataIndex: 'capital_spent',
                    // editable: true,
                },
                {
                    title: 'Lợi nhuận',
                    dataIndex: 'profit',
                    // editable: true,
                },
                {
                    title: 'Action',
                    dataIndex: 'action',
                    render: (text, record) =>
                        this.state.rows.length >= 1 ? (
                            <Popconfirm title='Sure to delete?' onConfirm={() => this.handleDelete(record.key)}>
                                <Icon type='delete' theme='filled' style={{ color: 'red', fontSize: '20px' }} />
                            </Popconfirm>
                        ) : null,
                },
            ],
        };
    }
    componentDidMount() {
        this.getList().then()
    }
    async getList() {
        const items = await baseAPI.getAll();
        console.log('item', items); // MongLV log fix bug
        items && this.setState({
            rows: Object.values(items),
        })
    }

    handleSave = (row) => {
        const newData = [...this.state.rows];
        const index = newData.findIndex((item) => row.key === item.key);
        const item = newData[index];
        newData.splice(index, 1, {
            ...item,
            ...row,
        });
        this.setState({ rows: newData });
    };

    handleDelete = (key) => {
        const rows = [...this.state.rows];
        this.setState({ rows: rows.filter((item) => item.key !== key) });
    };
    handleAdd = () => {
        // const { count, rows } = this.state;
        // const newData = {
        //     key: count,
        //     name: "User's name",
        //     age: '22',
        //     gender: 'Female',
        // };
        // this.setState({
        //     rows: [newData, ...rows],
        //     count: count + 1,
        // });
    };

    render() {
        const components = {
            body: {
                row: EditableFormRow,
                cell: EditableCell,
            },
        };
        const columns = this.state.columns.map((col) => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: (record) => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.handleSave,
                }),
            };
        });
        return (
            <>
                <div style={{ margin: 50}}>
                    <Table
                        components={components}
                        rowClassName={() => 'editable-row'}
                        dataSource={this.state.rows}
                        columns={columns}
                    />
                </div>
            </>
        );
    }
}
